import React from 'react';

function Films({movi}) {

    return (
        <>
            <div className="card card_logo">
                <div className="card-image waves-effect waves-block waves-light">
                    <img className="activator" src={movi.Poster}/>
                </div>
                <div className="card-content">
                    <span className="card-title activator grey-text text-darken-4">{movi.Title}<i
                        className="material-icons right"></i></span>
                    <p>{movi.Type} <span>{movi.Year}</span></p>
                </div>
            </div>

        </>
    );
}

export default Films;