import Films from "./Films";
import React from 'react';
import Search from "./Search";
import Error from "./Error";

function Film(props) {
   const {movie=[]}=props;
    return (
        <>
            <div className='film'>
                {movie.length? movie.map(data => <Films movi={data} key={data.imdbID}/>): <Error/>}
            </div>
        </>
    );
}

export default Film;

